                    <div class="column">
                        <div class=" row item-center">
                            <div class="numberBox row center ">
                                <span class="number">{{item.number}}.</span>
                            </div>
                            <div class="diamond">

                            </div>
                        
                            <span class="title">{{item.title}}</span>
                        </div>
                        <div class="introduce">
                            <p>{{item.introduce}}</p>
                        </div>
                    </div>
                    <div class="imgBox">
                        <img class="imgBox " :src="item.img_src" alt="" >
                    </div>
                </div>

                <div class="row center  average box"  v-if="item.number%2 == 0" >
                    <div class="imgBox">
                        <img class="imgBox " :src="item.img_src" alt="" >
                    </div>
                    <div class="column">
                        <div class=" row item-center">
