import hader from '@/components/header';
import footera from '@/components/footer';
import advantageModule from '@/components/advantageModule';
export default {
    name:'advantage',
    components:{
        hader,
        footera,
        advantageModule,
    },
    data(){
        return{
            data:[],
            nav:'advantage',
            img:{
