                img_src:require('@/assets/img/advantage4.png')
            },
            {
                number:5,
                title:'系统自开彩种自定义杀率',
                introduce:'除官方彩种外，系统自开彩种，可自定义KJ。 ',
                img_src:require('@/assets/img/advantage5.png')
