                img_src:require('@/assets/img/advantage12.png')
            },
            {
                number:13,
                title:'多种支付方式',
                introduce:'支持支付宝，微信，QQ支付，银联汇款等多种支付方式 ',
                img_src:require('@/assets/img/advantage13.png')
            },
            {
                number:14,
                title:'每月好运金',
                introduce:'每月有效投注，月月领取好运彩金，让玩家爱不释手，大大增加玩家的留存率。 ',
                img_src:require('@/assets/img/advantage14.png')
            },
            {
                number:15,
                title:'每天签到彩金',
                introduce:'根据玩家VIP等级，天天领取惊喜红包，大大增加玩家的在线率。 ',
