        height: 100%;
    }
    .nav>div>div{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        height: 50%;
    }
    .nav>div img{
        width: .34rem;
        height: .34rem;
    }
    .list{
        width: 100%;
        height: 100%;
        position: absolute;
        overflow-x: hidden;
        overflow-y: auto;
    }
    .active{
