    }
    .page{
        /* padding: 0 0.2rem; */
        min-height: 3rem;
        margin-bottom: 0.2rem;
        background: #F5F5F5;
    
    }
    .numberBox{
        width:0.42rem;
        height:0.4rem;
        background:linear-gradient(90deg,rgba(230,52,64,1) 0%,rgba(211,9,22,1) 100%);
        border-radius: 10%;
        z-index: 1;
    }
    .diamond{
        position:relative;
        width: 0.1rem;
        height: 0.1rem;
        background:linear-gradient(90deg,rgba(230,52,64,1) 0%,rgba(211,9,22,1) 100%);
        transform: rotate(40deg);
        left: -0.05rem;
    }
    .number{
        font-size: 0.3rem;
        color: #ffffff;
        
    }
    .title{
        font-size:0.3rem;
        font-weight:bold;
        color:rgba(85,85,85,1);
    }
    .imgBox{
        width: 2.32rem;
        height: 2.32rem;
    }
    .introduce{
        width: 3.78rem;
        font-size: 0.24rem;
        font-weight:400;
        color:rgba(102,102,102,1);
    }
</style>





